import React from "react";
import useDimensions from "../../hooks/useDimensions";

import { ScrollerContainer, ScrollerContent } from "./components";

const HorizontalScroller = ({ items, reverse, height }) => {
  const { width } = useDimensions();

  return (
    <ScrollerContainer height={height}>
      <ScrollerContent reverse={reverse} viewportw={width}>
        {items}
      </ScrollerContent>
    </ScrollerContainer>
  );
};

export default HorizontalScroller;
