import styled, { keyframes } from "styled-components";

export const FadeIn = keyframes`
  0% { opacity: 0; }
  100% { opacity: 1; }
 `;
export const SlideIn = keyframes`
  0% { padding-left: 0; }
  100% { padding-left: 200; }
 `;
export const Scroll = keyframes`
  0% { margin-left: 0; }
  50% { margin-left: Calc(100vw - 2836px); }
  100% { margin-left: 0; }
 `;

export const PageLayout = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
  & * {
    /* width */
    ::-webkit-scrollbar {
      width: 2px;
      height: 2px;
    }

    /* Track */
    ::-webkit-scrollbar-track {
      background: #f1f1f1;
    }

    /* Handle */
    ::-webkit-scrollbar-thumb {
      background: #888;
    }

    /* Handle on hover */
    ::-webkit-scrollbar-thumb:hover {
      background: #555;
    }
  }
`;

