import React, { useCallback, useEffect, useMemo, useState } from "react";
import IMAGES from "../../constants/images";

import { SliderContainer, SliderControls, SliderSwitcher } from "./components";

const BackgroundSlider = () => {
  const [activeId, setActiveId] = useState(1);
  const [timer, setTimer] = useState();

  const bgs = useMemo(
    () => [
      {
        id: 0,
        url: IMAGES.Background1,
      },
      { id: 1, url: IMAGES.Background0 },
      {
        id: 2,
        url: IMAGES.Background2,
      },
      {
        id: 3,
        url: IMAGES.Background3,
      },
    ],
    []
  );

  const nextBG = useCallback(() => {
    setActiveId((id) => id + 1);
  }, []);

  const bgsCount = useMemo(() => bgs.length, [bgs]);
  useEffect(() => {
    const interval = setInterval(nextBG, 5000);
    setTimer(interval);
    return () => clearInterval(interval);
  }, [nextBG]);
  return (
    <>
      {bgs.map((bg) => (
        <SliderContainer key={bg.id} bg={bg.url} active={activeId % bgsCount === bg.id} />
      ))}
      <SliderControls>
        {bgs.map((bg) => (
          <SliderSwitcher
            key={bg.id}
            active={activeId % bgsCount === bg.id}
            onClick={() => {
              setActiveId(bg.id);
              clearInterval(timer);
              const interval = setInterval(nextBG, 5000);
              setTimer(interval);
            }}
          />
        ))}
      </SliderControls>
    </>
  );
};

export default BackgroundSlider;
